	module.exports = {"data":{"post":{"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2011/11/tree.jpg\" alt=\"Tree\" border=\"0\" width=\"448\" height=\"600\" /></p>\n<p>I was impressed by the view from our back window this morning. The lawn was frozen and the fog was thick and this leafless tree seemed to be spiting it all. It's November, and the damp cold should come as no surprise. The heat is on and we've started lighting the wood stove in the evening. The holidays will be here before we know it, and we're talking about next year like it's just around the corner instead of the distant future. </p>\n<p>The fog is frozen and this tree is spiting it all.</p>","fields":{"title":"Frozen Fog","date":"13 Nov 2011","url":"https://papascott.de/2011-11-13-frozen-fog/"}}},"pathContext":{"slug":"/2011-11-13-frozen-fog/","prev":{"fields":{"title":"Off The Rack","date":"02 Oct 2011","slug":"/2011-10-02-off-the-rack/"}},"next":{"fields":{"title":"Unimaginable","date":"19 Nov 2011","slug":"/2011-11-19-unimaginable/"}}}}

